import Vue from 'vue'
import Vuex from 'vuex'
import userAuth from './helpers/user-auth'
import user from './helpers/user'
import ajax from './helpers/ajax'
import localforage from 'localforage'
// import testData from './helpers/test-data'
import eventActions, {mutations as eventMutations} from './store.event'

Vue.use(Vuex)

localforage.config({
    // driver      : localforage.WEBSQL, // Force WebSQL; same as using setDriver()
  name: 'futsalparis'
    // version     : 1.0,
    // size        : 4980736, // Size of database, in bytes. WebSQL-only for now.
    // storeName   : 'futsalparis', // Should be alphanumeric, with underscores.
    // description : 'some description'
})

const state = {
  user: {
    id: '',
    role: '',
    name: '',
    email: '',
    refreshToken: '',
    accessToken: '',
    isLogged: false
  },
  router: {
    redirect: '',
    current: ''
  },
  files: [],
  events: []// testData.events
}

var mutations = {
  currentRouteChange (state, current) {
    state.router.current = current
  },
  userData (state, data) {
    state.user.id = data.id
    state.user.role = data.role
    state.user.name = data.name
  },
  userSignIn (state, {email, refreshToken, accessToken}) {
    state.user.email = email
    state.user.refreshToken = refreshToken
    state.user.accessToken = accessToken
    localforage.setItem('accessToken', state.user.accessToken)
    localforage.setItem('refreshToken', state.user.refreshToken)
    localforage.setItem('userEmail', state.user.email)
    ajax.assignAuthHeaders({
      Authorization: `Bearer ${accessToken}`
    })
    state.user.isLogged = true
  },
  accessTokenChange (state, {accessToken}) {
    state.user.accessToken = accessToken
    ajax.assignAuthHeaders({
      Authorization: `Bearer ${accessToken}`
    })
  }
}

Object.assign(mutations, eventMutations)

var actions = {
  async syncModulesList ({state, commit}) {
    state.files = []
  },
  async authenticateWithFacebook ({state, commit}, {accessToken}) {
    let rta = await userAuth.loginWithFacebook(accessToken)
    if (rta.status === 200) rta = await rta.json()
    commit('userSignIn', {
      email: rta.user.email,
      refreshToken: rta.token.refreshToken,
      accessToken: rta.token.accessToken
    })
    console.log('authenticateWithFacebook', rta)
  },
  async updateMyProfileDetails ({commit}) {
    let rta = await user.getProfileDetails()
    if (rta.status === 200) rta = await rta.json()
    commit('userData', rta)
    console.log('updateMyProfileDetails', rta)
  },
  async refreshToken ({state, commit}) {
    let accessToken = await localforage.getItem('accessToken')
    let token = await localforage.getItem('refreshToken')
    let email = await localforage.getItem('userEmail')
    state.user.email = email
    state.user.refreshToken = token
    if (!email) return console.log('refreshToken: Email empty, skip')
    if (!token) return console.log('refreshToken: refreshToken empty, skip')
    commit('accessTokenChange', {accessToken})
    let rta = await userAuth.refreshToken(email, token)
    if (rta.status === 200) rta = await rta.json()
    if (rta.status === 401) return console.warn('Unable to refresh session.')
    console.log('refreshToken', rta)
    commit('userSignIn', {
      email: state.user.email,
      refreshToken: rta.refreshToken,
      accessToken: rta.accessToken
    })
  }
}

Object.assign(actions, eventActions)

const getters = {
  loggedUserId: state => state.user.id,
  userIsLogged: state => state.user.isLogged,
  routerRedirect: state => state.router.redirect,
  currentRoute: state => state.router.current,
  files: state => state.files,
  events: state => state.events
}

export default new Vuex.Store({
  state,
  getters,
  actions,
  mutations
})



